import React from 'react'
import image1 from './images/image1.jpg'
import image2 from './images/image2.jpg'

class Tile extends React.Component {
    constructor() {
        super()
        this.wrapper = React.createRef();
         this.reveal = this
            .reveal
            .bind(this)
    }

    reveal() {
        const node = this.wrapper.current;
        const rect = node.getBoundingClientRect();
        const dist = rect.top
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.add('visible'), 500)
        } else {
            setTimeout(() => node.classList.remove('visible'), 500)
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.reveal)

    }

    render() {
        const {img, alt, classy} = this.props
        return (
            <div ref={this.wrapper} className={classy}>
                <div className="row tile">
                    <img className="tileimg" src={img} alt={alt}/>
                    <div className="inner-bottom mgbt-1 mgbt-m-2">
                        <h3 className="inner-title light mgbt-text-10">For him</h3>
                        <a className="text-s light" href="#">all products</a>
                    </div>
                </div>
            </div>
        )
    }
}

class ProductTile extends React.Component {
    constructor() {
        super()
        this.wrapper = React.createRef();
        this.reveal = this
            .reveal
            .bind(this)
    }

    reveal() {
        const node = this.wrapper.current;
        const rect = node.getBoundingClientRect();
        const dist = rect.top
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.add('visible'), 500)
        } else {
            setTimeout(() => node.classList.remove('visible'), 0)
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.reveal)

    }

    render() {
        const {img, alt, classy, visible} = this.props

        return (
            <div ref={this.wrapper} className={classy}>
                <div className="row tile">
                    <img className="tileimg" src={img} alt={alt}/>
                </div>
                <a className="text-s dark mgt-text-10" href="#">See product</a>
            </div>
        )
    }
}

