import React from 'react';

class Navigation extends React.Component {
    constructor() {
        super()

        this.wrapper = React.createRef();
        this.state = {
            height: '',
            position: 0,
        }

        this.fixed = this
            .fixed
            .bind(this)
    }

    fixed() {
        const node = this.wrapper.current;
        const limit = node.offsetHeight;
        if (window.scrollY + 143 > window.innerHeight) {
            this.setState({height: 'navtop'})
        } else {
            this.setState({height: ''})
        }
        console.log(node.offsetHeight)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.fixed)

    }

    render() {
        const {height} = this.state
        const classname = `row bgred ${height}`
        return (
            <div ref={this.wrapper} className={classname}>
                <div
                    className="offset-9 col-7 offset-s-3 col-s-19 align-center justify-between">
                    <a className="navigation-link light" href="#">For him</a>
                    <a className="navigation-link light" href="#">For her</a>
                    <a className="navigation-link light" href="#">For kids</a>
                </div>
            </div>
        )
    }
}

export default Navigation